<template>
    <div class="detailsed">
        <div class="top"></div>
        <div>
            <div class="bj" :style="{'background-image': 'linear-gradient(to bottom, rgba(245,245,245, 0), rgba(245,245,245, .85) 50%, rgba(245,245,245, 1) 60%), url(' + movieDetailData.images.large + ')'}"></div>
            <div>
                <van-nav-bar
                    title="电影详情"
                    left-text="返回"
                    left-arrow
                    @click-left="onClickLeft"
                />
            </div>
            
            <div class="details-info">
                <div class="details-box clearfix">
                    <div class="clearfix">
                        <div class="details-img fl">
                            <img class="auto-img" :src="movieDetailData.images.small" alt="">
                        </div>
                        <div class="details-msg fl">
                            <div class="clearfix">
                                <span class="fl title one-text">{{movieDetailData.title}}</span>
                                <span class="fr like" @click="likeMovie(movieDetailData)" :class="{active: likeActive}"> 
                                    <van-icon name="like-o" />
                                </span>
                            </div>
                            <div class="genres">
                                <span>{{movieDetailData.genresInfo}}</span>
                            </div>
                            <div class="clearfix comments">
                                <div class="fl">
                                    <van-icon name="comment-circle-o" />
                                    <span class="comments-icon">{{movieDetailData.comments}}</span>
                                </div>
                                <div class="fl">
                                    <van-icon name="underway-o" />
                                    <span class="comments-icon">{{movieDetailData.durations[0]}}</span>
                                </div>
                            </div>
                            <div class="score clearfix">
                                <div class="fl clearfix star-box-count">
                                    <!-- 灰星 -->
                                    <div ref="noactivestar" class="noactive-star fl">
                                        <span>
                                        <van-icon name="star-o" />
                                        </span>
                                        <span>
                                        <van-icon name="star-o" />
                                        </span>
                                        <span>
                                        <van-icon name="star-o" />
                                        </span>
                                        <span>
                                        <van-icon name="star-o" />
                                        </span>
                                        <span>
                                        <van-icon name="star-o" />
                                        </span>
                                    </div>
                                    <!-- 黄星 -->
                                    <div class="active-star" :style="{width: movieDetailData.width + 'px'}">
                                        <span>
                                        <van-icon name="star-o" />
                                        </span>
                                        <span>
                                        <van-icon name="star-o" />
                                        </span>
                                        <span>
                                        <van-icon name="star-o" />
                                        </span>
                                        <span>
                                        <van-icon name="star-o" />
                                        </span>
                                        <span>
                                        <van-icon name="star-o" />
                                        </span>
                                    </div>
                                </div>
                                <!-- 分数 -->
                                <div class="fraction fl">{{movieDetailData.rating.average}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="film-generalization">
                        <div class="generalization">
                            <span>电影概括</span>
                        </div>
                        <div class="details-text">
                            <p class="p-text" :style="{display}">{{movieDetailData.summary}}</p>
                            <!-- 箭头 -->
                            <div class="arrow" @click="changeDisplay">
                                <van-icon name="arrow-down" />
                            </div>
                        </div>
                    </div>
                </div>

                <div class="tab-box">
                    <van-tabs v-model="active">
                        <van-tab title="影人信息">
                            <div class="yanyuan">
                                <div class="yanyuan-box clearfix" :style="{width: movieDetailData.casts.length * movieDetailData.w - 20 + 'px'}">
                                    <div class="yanyuan-item fl" ref="yanyuanitem" v-for="(item, index) in movieDetailData.casts" :key="index">
                                        <div class="name-img">
                                            <img class="auto-img" :src="item.avatars.small" alt="">
                                        </div>
                                        <div class="name one-text">{{item.name}}</div>
                                    </div>
                                </div>
                            </div>
                        </van-tab>
                        <van-tab title="电影剧照">内容 2</van-tab>
                        <van-tab title="热门评论">内容 3</van-tab>
                    </van-tabs>
                </div>
                <div class="buy-btn" @click="buyTick(movieDetailData.id)">立马购票</div>
            </div>

        </div>
    </div>
</template>

<script>
    import {createNamespacedHelpers} from 'vuex'

    const {mapState, mapMutations} = createNamespacedHelpers('movieData')
export default {
    name: 'detailsed',

    data() {
        return {
            active: 0
        };
    },

    computed: {
      ...mapState(['movieDetailData', 'display', 'likeActive'])
    },

    methods: {
        ...mapMutations(['changeDisplay', 'resetState']),

        // 返回上一步
        onClickLeft() {
            this.$router.go(-1);
        },
            
        //购票
        buyTick(id) {
            this.$router.push({name: 'buyMovieTick', query: {id}});
        },

        // 收藏电影
        likeMovie(item) {
            // console.log('item :', item);

            //获取用户登录状态
            let userStatus = localStorage.getItem('user');
            // console.log('userStatus :', userStatus);

            if (userStatus) {

                userStatus = JSON.parse(userStatus);
                if (!userStatus.isLogin) {
                    // console.log('请先登录');
                    return this.$router.push({name: 'login'});
                }

                //获取用户收藏的电影
                let likeMovieData = localStorage.getItem('likeMovieData');
                likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : {};

                // 记录当前用户收藏的电影
                let currentLikeMovieData = likeMovieData[userStatus.username];

                if (!currentLikeMovieData) {
                    likeMovieData[userStatus.username] = [];
                }

                // 收藏电影之前，根据电影的id判断是否已经收藏过该电影
                for (let i = 0; i < likeMovieData[userStatus.username].length; i++) {
                    if (likeMovieData[userStatus.username][i].id == item.id) {
                        //提示用户
                        this.$toast('你已经收藏过该电影');
                        return;
                    }
                }
                
                this.$store.commit('movieData/changelikeActive', true);

                //将电影推进likeMovieData[userStatus.username]
                likeMovieData[userStatus.username].unshift(item);
                // console.log('likeMovieData :', likeMovieData);

                localStorage.setItem('likeMovieData', JSON.stringify(likeMovieData));

                this.$toast.success('收藏成功');
            } else {
                // console.log('请先登录');
                this.$router.push({name: 'login'});
            }

        }
    },

    created() {

        //开启加载提示
        this.$toast.loading({
            duration: 0,
            message: '加载中...'
        });

        //重置state
        this.resetState();
        
        //截取路由参数
        let id = this.$route.params.id;
        // console.log('id :', id);

        this.axios({
            method: 'GET',
            url: 'https://douban.uieee.com/v2/movie/subject/' + id
        }).then(result => {
            
            //将电影类型合并
            result.data.genresInfo = result.data.genres.join(' / ');
            // console.log(result.data.genresInfo);

            //超过1000, 不足10000，以k显示，超过10000以w显示，不足1000，直接显示
            let comments = result.data.comments_count;

            result.data.comments = comments >= 10000 ? (comments / 10000).toFixed(1) + 'w' : comments >= 1000 ? (comments / 1000).toFixed(1) + 'k' : comments;

            // console.log('result :', result);

            // 获取星星的宽度
            let notstarWidth = this.$refs.noactivestar.clientWidth;
            // console.log('notstarWidth :', notstarWidth);

            result.data.width = result.data.rating.average / result.data.rating.max * notstarWidth;

            // this.$store.commit('movieData/getMovieDetailData', result.data)

            // 获取演员的图片盒子宽度
            result.data.w = 0;
            this.$store.dispatch('movieData/getMovieDetailData', result.data)
            .then(() => {
                this.$toast.clear();

                let yanyuanitemWidth = this.$refs.yanyuanitem[0].clientWidth;
                // console.log('yanyuanitemWidth :', yanyuanitemWidth);
                this.$store.commit('movieData/changeW', yanyuanitemWidth);

                //根据用户登录状态，判断该电影是否被该用户收藏
                let userStatus = localStorage.getItem('user');
                let isActive = userStatus && JSON.parse(userStatus).isLogin;

                // 如果用户登录
                if (isActive) {
                    let username = JSON.parse(userStatus).username;

                    // 所有用户收藏的电影
                    let likeMovieData = localStorage.getItem('likeMovieData');

                    likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : {};

                    // 获取当前用户收藏的电影
                    let currentLikeMovieData = likeMovieData[username];

                    currentLikeMovieData = currentLikeMovieData ? currentLikeMovieData : [];

                    for (let i = 0; i < currentLikeMovieData.length; i++) {
                        if (this.movieDetailData.id == currentLikeMovieData[i].id) {
                            this.$store.commit("movieData/changelikeActive", true);
                            return;
                        }
                    }

                    this.$store.commit('movieData/changelikeActive', false);
                } else {
                    this.$store.commit('movieData/changelikeActive', false);
                }
            })
        }).catch(err => {
            // console.log('err :', err);
            this.$toast.clear();
        })
    }
}
</script>

<style lang="less" scoped>
    .detailsed {
        .top {
            background-color: #000;
            height: 24px;
        }
        
        .bj {
            position: fixed;
            top: 24px;
            bottom: 0;
            left: 0;
            right: 0;
            background:  no-repeat center center;
            background-size: 100% 100%;
        }
        .details-info {
            position: fixed;
            top: 70px;
            left: 0;
            bottom: 0;
            right: 0;
            overflow-y: auto;
            z-index: 2;
        }
        .details-box {
            background-color: #fff;
            margin: 100px 10px 0;
            border-radius: 10px;
        }
        .details-img {
            width: 100px;
            margin-top: -20px;
            padding: 0 10px;

            .auto-img {
                border-radius: 6px;
            }
        }
        .details-msg {
            width: calc(~"100% - 120px");
            margin: 10px 0;
        }
        .title {
            font-size: 16px;
            width: 85%;
        }
        .like {
            font-size: 18px;
            margin-right: 10px;
        }
        .like.active {
            color: #FF9E01;
        }

        .genres {
            font-size: 14px;
            margin: 10px 0;
        }
        .comments {
            font-size: 14px;
            margin-bottom: 10px;
        }
        .comments-icon {
            margin: 0 10px 0 4px;
        }
        .score {
            position: relative;
            font-size: 16px;
            text-align: center;

            .active-star {
                position: absolute;
                top: 0;
                left: 0;
                color: #FDC03D;
                height: 16px;
                width: 60%;
                overflow: hidden;
            }
            
            .noactive-star {
                width: 80px;
                height: 16px;
            }
            .star-box-count {
                width: 80px;
                position: relative;
                span {
                position: absolute;
                width: 16px;
                height: 16px;
                top: 0;
                &:nth-child(1){
                left: 0;
                }
                &:nth-child(2){
                    left: 16px;
                }
                &:nth-child(3){
                    left: 32px;
                }
                &:nth-child(4){
                    left: 48px;
                }
                &:nth-child(5){
                    left: 64px;
                }
                }
            }
            .fraction {
                font-size: 16px;
                margin-left: 20px;
                color: #FDC03D;
                width: 20px;
            }
        }

        .film-generalization {
            margin: 10px;
        }
        .generalization {
            font-size: 16px
        }
        .details-text {
            font-size: 12px;
        }
        .p-text {
            color: #666;
            line-height: 18px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3;
            overflow: hidden;
        }

        .arrow {
            text-align: center;
        }

        .tab-box {
            margin: 30px 10px 0;
            background-color: #fff;
            border-radius: 10px;
            overflow: hidden;
        }
        .yanyuan {
            text-align: center;
            margin: 16px 10px 10px;
            font-size: 14px;
            overflow-y: hidden;
            overflow-x: auto;
            .yanyuan-box {
                width: 540px;
            }
            .name-img {
                
                border-radius: 6px;
                overflow: hidden;
            }
            
            .yanyuan-item {
                width: 100px;
                padding-right: 20px;
                &:last-child {
                    padding-right: 0;
                }
            }
            .name {
                padding: 8px 0;
            }
        }

        .buy-btn {
            width: 120px;
            height: 40px;
            line-height: 40px;
            font-size: 16px;
            font-weight: 700;
            text-align: center;
            margin: 20px auto;
            color: #333;
            background-image: linear-gradient(to bottom, #FED73E, #FFBE46);
            border-radius: 20px;
            box-shadow: 0 0 2px #FF9A00;
            letter-spacing: 3px;
        }
    }
</style>